<template>
  <div class="text-xs-center wrapper">
    <div>
      <v-dialog v-model="dialog" width="700">
        <v-card>
          <v-card-title class="headline" primary-title>Вход по паролю</v-card-title>
          <v-card-text>
            <v-form>
              <v-text-field v-model="login" label="Логин"></v-text-field>
              <v-text-field v-model="password" type="password" label="Пароль"></v-text-field>
              <v-btn color="success" @click="passwordLogin">Войти</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
    <v-container fluid grid-list-xl style="padding-top: 0;">
      <v-layout row wrap>
        <v-flex md12 xs12>
          <v-card>
            <v-subheader>Ваш аккаунт</v-subheader>
            <v-card-text>
              <v-layout row>
                <v-flex xs9>
                  <v-form class="text-xs-left">
                    <div>
                      <v-text-field
                        v-model="token"
                        @change="tokenUpdated"
                        label="Токен"
                        required
                        style="max-width: 300px;padding-bottom:0px; margin-bottom:0px;"
                      ></v-text-field>
                      <a @click="dialog = true">или вход через логин и пароль</a>
                    </div>
                    <v-text-field
                      v-bind:value="(myself && myself.first_name + ' ' + myself.last_name) || ''"
                      label="Имя"
                      readonly
                      disabled
                    ></v-text-field>
                    <v-btn v-if="inittingStage === 0" color="success" @click="init">Войти</v-btn>
                    <v-btn v-else color="success" disabled>Войти</v-btn>

                    <v-progress-circular v-if="inittingStage === 1" indeterminate color="primary"></v-progress-circular>
                  </v-form>
                </v-flex>

                <v-flex xs3>
                  <v-img
                    class="round"
                    aspect-ratio="1"
                    contain
                    :src="(myself && myself.photo_200) || 'https://placehold.jp/100x100.png'"
                  ></v-img>
                </v-flex>
              </v-layout>
              <v-alert :value="error" type="error">{{ error }}</v-alert>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex md12 xs12>
          <v-card>
            <v-subheader>Фото</v-subheader>

            <div>
              <v-tabs centered v-model="activeTab" show-arrows>
                <v-tabs-slider color="blue"></v-tabs-slider>
                <v-tab
                  v-for="(content, i) in photos"
                  :key="`${chats[i].peer_id}`"
                >{{chats[i].peerInfo.first_name}} {{chats[i].peerInfo.last_name}}</v-tab>
                <v-tabs-items>
                  <v-tab-item v-for="(content, i) in photos" :key="`${chats[i].peer_id}`">
                    <v-layout>
                      <v-flex>
                        <v-container grid-list-sm fluid>
                          <v-layout row wrap v-if="chats[i].downloaded">
                            <v-flex
                              v-for="(photo, j) in photos[i].slice(0, chats[i].currentMax)"
                              :key="`${j}_${photo.id}`"
                              xs2
                              d-flex
                            >
                              <v-card flat tile class="d-flex">
                                <v-img
                                  @click='index = j;lbVisible = true; '
                                  :src="photo.lowRes"
                                  :lazy-src="photo.lowRes"
                                  class="grey lighten-2"
                                  aspect-ratio="1"
                                >
                                  <template v-slot:placeholder>
                                    <v-layout fill-height align-center justify-center ma-0>
                                      <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                                    </v-layout>
                                  </template>
                                </v-img>
                              </v-card>
                            </v-flex>
                            <VueEasyLightbox
                              :visible="lbVisible"
                              :imgs="photos[i].map(e => e.highRes)"
                              :index="index"
                              @hide="lbVisible = !lbVisible"
                            ></VueEasyLightbox>
                          </v-layout>
                          <v-layout v-else fill-height align-center justify-center ma-0>
                            <v-progress-circular indeterminate></v-progress-circular>
                          </v-layout>
                        </v-container>
                        <v-btn
                          color="success"
                          v-if="chats[i].downloaded && (photos[i].length > chats[i].currentMax)"
                          @click="chats[i].currentMax += 12"
                        >Больше</v-btn>
                      </v-flex>
                    </v-layout>
                  </v-tab-item>
                </v-tabs-items>
              </v-tabs>
            </div>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
// import { saveAs } from "file-saver";
// import JSZip from "jszip";
import VueEasyLightbox from 'vue-easy-lightbox'
import axios from "axios";
import API from "../libs/api";
import { setTimeout } from 'timers';

export default {
  name: "Main",
  data: function() {
    return {
      activeTab: null,
      dialog: false,
      token: null,
      myself: null,
      inittingStage: 0,
      error: false,
      login: "",
      password: "",
      chats: [],
      photos: [],
      peerInfoByChatId: {},
      lbVisible: false,
      index: 0
    };
  },
  created: function() {
    // if (process.env.NODE_ENV === "development") {
    //   this.token = localStorage.getItem("ps_token");
    //   API.token = this.token;
    //   this.init();
    // }
  },
  methods: {
    maxRes: function(photo) {
      var values = "photo_130 photo_604 photo_807 photo_1280 photo_2560".split(
        " "
      );
      var ans = [0, 0];
      values.forEach(function(f) {
        if (photo[f]) {
          ans[0] = photo[f];
        }
      });
      if (photo.photo_130) {
        ans[1] = photo.photo_130;
      } else {
        values.reverse().forEach(function(f) {
          if (photo[f]) {
            ans[1] = photo[f];
          }
        });
      }
      return ans;
    },
    passwordLogin: function() {
      this.dialog = false;
      this.inittingStage = 1;
      axios
        .post("https://apidog.ru/api/v2/apidog.authorize", {
          login: this.login,
          password: this.password,
          application: 1
        })
        .then(d => {
          d = d.data;
          if (d.response.errorId === 73) {
            this.error =
              "ВК просит ввести капчу, а я эту функцию не сделал. Ждите.";
            this.tokenUpdated();
            return;
          } else if (!d.response.userAccessToken) {
            this.error = "Неверная пара логин/пароль.";
            this.tokenUpdated();
            return;
          }
          this.token = d.response.userAccessToken;
          this.tokenUpdated();
          this.init();
        });
    },
    tokenUpdated: function() {
      this.inittingStage = 0;
      this.myself = null;
      this.photos = [];
      API.token = this.token;
    },
    init: function() {
      this.error = false;
      this.inittingStage = 1;
      API.loadVK(
        "users.get",
        { fields: "photo_50, photo_100, photo_200" },
        d => {
          if (!d) {
            this.error = "Токен не валиден или недостаточно прав.";
            this.tokenUpdated();
            return;
          }
          this.myself = d[0];
          API.uid = d[0].id;
          API.myself = d[0];
        },
        e => {
          this.error = `Ошибка сети (${e})`;
          this.tokenUpdated();
        }
      );

      var str = [];

      for (var i = 0; i < 25; i++) {
        str.push(
          `API.messages.getConversations({offset: ${i *
            40}, count:40, extended: 1, v:5.80})`
        );
      }

      API.loadVK(
        "execute",
        {
          code: "return[" + str.join(",") + "];"
        },
        data => {
          if (!data) {
            this.error = "Токен не валиден или недостаточно прав.";
            this.tokenUpdated();
            return;
          }

          let ans = [];
          for (let idx = 0; idx < data.length; idx++) {
            let d = data[idx];
            for (let i = 0; i < d.items.length; i++) {
              let pid = d.items[i].conversation.peer.id;
              let conv = d.items[i].conversation;
              if (conv.peer.type === "user") {
                for (let j = 0; j < d.profiles.length; j++) {
                  if (d.profiles[j].id === pid) {
                    ans.push({
                      type: "user",
                      peer_id: pid,
                      last_message: d.items[i].last_message,
                      peerInfo: d.profiles[j],
                      selected: true
                    });
                    d.items[i].peerInfo = d.profiles[j];
                    break;
                  }
                }
              } else if (conv.peer.type === "group") {
                for (let j = 0; j < d.groups.length; j++) {
                  if (-d.groups[j].id === conv.peer.id) {
                    let info = {
                      id: -d.groups[j].id,
                      last_name: "",
                      first_name: d.groups[j].name,
                      photo_100: d.groups[j].photo_100,
                      photo_50: d.groups[j].photo_50
                    };
                    ans.push({
                      type: "group",
                      peer_id: pid,
                      last_message: d.items[i].last_message,
                      peerInfo: info,
                      selected: true
                    });
                    d.items[i].peerInfo = info;
                    break;
                  }
                }
              } else if (conv.peer.type === "chat") {
                let info = {
                  id: conv.peer.id,
                  last_name: "",
                  first_name: conv.chat_settings.title,
                  photo_100: conv.chat_settings.photo
                    ? conv.chat_settings.photo.photo_100
                    : "https://vk.com/images/camera_100.png?ava=1",
                  photo_50: conv.chat_settings.photo
                    ? conv.chat_settings.photo.photo_50
                    : "https://vk.com/images/camera_50.png?ava=1"
                };
                ans.push({
                  type: "chat",
                  peer_id: conv.peer.id,
                  last_message: d.items[i].last_message,
                  peerInfo: info,
                  selected: true
                });
              }
              ans[ans.length - 1].downloaded = false;
              ans[ans.length - 1].currentMax = 12;
            }
          }
          this.chats = ans;

          this.chats = this.chats.slice(0, 20);

          this.inittingStage = 3;

          for (let i = 0; i < this.chats.length; i++) {
            this.peerInfoByChatId[this.chats[i].peer_id] = this.chats[
              i
            ].peerInfo;
          }
          this.showPhotos();
        },
        e => {
          this.error = `Ошибка сети (${e})`;
          this.tokenUpdated();
        }
      );
    },
    loadAllAttachments: function(peer_id, callback) {
      let db = [];
      let start = function(peer_id, offset, callback) {
        if (offset === false) return;
        var str = ['var a; var ans =[];'];
        for (var i = 0, l = 21; i < l; ++i) {
          str.push(
            `a = API.messages.getHistoryAttachments({peer_id:${peer_id},v:5.2,start_from:a.next_from,count:200, media_type: 'photo'}); ans.push(a.items);`
          );
        }

        API.loadVK(
          "execute",
          {
            code: str.join(';') + `; return ans;`
          },
          function(data) {
            if ((data = saveMessages(data)).isFull) {
              setTimeout(function() {
                start(peer_id, offset + 25 * 200, callback);
              }, 400);
            } else {
              callback(db);
            }
          }
        );
      };
      let saveMessages = function(data) {
        var messages = [],
          isFull = true;
        data.forEach(function(item) {
          if (Array.isArray(item)) messages = messages.concat(item);
        });
        isFull = data[data.length - 1] && data[data.length - 1].length == 200;
        data = null;
        for (let i = 0; i < messages.length; i++) {
          db.push(messages[i].photo);
        }
        return {
          messages: messages,
          isFull: isFull
        };
      };
      start(peer_id, 0, callback);
    },
    showPhotos: function() {
      for (let i = 0; i < this.chats.length; i++) {
        this.photos.push(null);
        setTimeout(() => 
        this.loadAllAttachments(this.chats[i].peer_id, x => {
          this.chats[i].downloaded = true;
          this.photos[i] = x;
          for (let j = 0; j < this.photos[i].length; j++) {
            this.photos[i][j].highRes = this.maxRes(this.photos[i][j])[0];
            this.photos[i][j].lowRes = this.maxRes(this.photos[i][j])[1];
          }
        }), 1000*i);
      }
    }
  },
  components: { VueEasyLightbox }
};
</script>

<style>
.wrapper {
  margin: auto;
  text-align: center;
  max-width: 1000px;
}
.round {
  max-height: 125px;
  max-width: 125px;
  border-radius: 50%;
  margin-top: 23px;
  margin: auto;
}
</style>
